import { Component , OnInit , ViewChild } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router, NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl } from '@angular/forms';
import { ReplaySubject} from 'rxjs';
import { IonInfiniteScroll } from '@ionic/angular';
import { UserCreatePage } from './user-create/user-create.page';
@Component({
selector: 'page-user-list',
templateUrl: 'user-list.page.html',
styleUrls: ['./user-list.page.scss'],
})
export class UserListPage implements OnInit {
@ViewChild(IonInfiniteScroll, { static : false }) infiniteScroll: IonInfiniteScroll;
rangeForm: FormGroup;
searchQuery: string;
showAgeFilter = false;
showmenu = false;
items: any  = [] ;
variables: any = [];
userinfo: any = [];
idasociado:any;
iddispositivo:any;
idllave: any;
public gLimit = 150;
page = 1;
maximumPages = 3;
viewType = 'grid';
tituloxx = '';
efecto = 0;
seguro = '';
ndisp: any;
msg = '';
apifoto: String;
itemsList: Array<any>;
public currentSegment = 'Todos';
searchSubject: ReplaySubject<any> = new ReplaySubject<any>(1);
constructor(  private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
   public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
   ) {  this.apifoto = this.authenticationService.getapifoto();
 }
changeToList() { this.viewType = 'list'; }
changeToGrid() {   this.viewType = 'grid';  }
ngOnInit() {
    this.variables = this.router.getCurrentNavigation().extras.state.variables;
    this.idllave = this.router.getCurrentNavigation().extras.state.idllave;
    this.idasociado = this.router.getCurrentNavigation().extras.state.idasociado;
    this.iddispositivo = this.router.getCurrentNavigation().extras.state.iddispositivo;
    this.ndisp = this.router.getCurrentNavigation().extras.state.dispn;
    this.userinfo = this.authenticationService.getcopiauser();
    if(this.router.getCurrentNavigation().extras.state.title )
    {this.tituloxx =this.router.getCurrentNavigation().extras.state.title }
    else
    {this.tituloxx = this.variables.nombre ;}
    
    this.searchQuery = '';
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value;  });
    this.rangeForm = new FormGroup({    dual: new FormControl({lower: 1, upper: 100})   });
    this.currentSegment = 'todos';
    this.pideregistros( this.currentSegment);
  }
  filterContacts(event: any) {
    const selectedCategory = event.detail.value;
    this.currentSegment = selectedCategory;
    this.pideregistros(selectedCategory);
  }
  pideregistros0() {
   this.pideregistros(this.currentSegment);
  }
  pideregistros(op , infiniteScroll?) {
       this.itemsList = [];
       this.items =[]
       this.efecto = 0 ;



 if (this.userinfo[0].role === 'Administrador' ) {

  if(this.idllave)
  {
    this.authenticationService.getReg(op  + '/empresatodoasociado/' + this.userinfo[0].idempresa._id  + '/' +this.idllave , 'personals', 0).then((data) => {
      this.efecto = 1;
      this.items =  data;
      this.itemsList = this.items;
    }, (err) => {
      this.authenticationService.presentAlert(err.error , this.msg , '');
    });
  }
  else
  {
 this.authenticationService.getReg(this.gLimit + '/pagineo2x/' + this.page + '/' + this.userinfo[0].idempresa._id + '°' + op , 'personals', 0).then((data) => {
   this.items = this.items.concat(data['docs']);
   this.itemsList = this.items;
   this.efecto = 1;
   this.maximumPages = this.items.concat(data['pages']);
   if (infiniteScroll) {     infiniteScroll.complete();   }

}, (err) => {
  this.authenticationService.presentAlert(err.error , this.msg , '');
});}
} else {

  if(this.iddispositivo)
  {
    this.authenticationService.getReg(op  + '/empresatodoasociadodisp/' + this.userinfo[0].idempresa._id  + '/' +this.idllave + '°' + this.iddispositivo, 'personals', 0).then((data) => {
      this.efecto = 1;
      this.items =  data;
      this.itemsList = this.items;
    }, (err) => {
      this.authenticationService.presentAlert(err.error , this.msg , '');
    });
  }
  else
  {

  if(this.idllave)
  {
    this.authenticationService.getReg(op  + '/empresatodoasociado/' + this.userinfo[0].idempresa._id  + '/' +this.idllave, 'personals', 0).then((data) => {
      this.efecto = 1;
      this.items =  data;
      this.itemsList = this.items;
    }, (err) => {
      this.authenticationService.presentAlert(err.error , this.msg , '');
    });
  }
  else
  {
    this.authenticationService.getReg(op  + '/empresatodo/' + this.userinfo[0].idempresa._id , 'personals', 0).then((data) => {
      this.efecto = 1;
      this.items =  data;
      this.itemsList = this.items;
    }, (err) => {
      this.authenticationService.presentAlert(err.error , this.msg , '');
    });
  }
  }
 
}


  }
  searchList() {
    this.searchSubject.next({
     lower: this.rangeForm.controls.dual.value.lower,
     upper: this.rangeForm.controls.dual.value.upper,
     query: this.searchQuery
   });
    const query = (this.searchQuery && this.searchQuery !== null) ? this.searchQuery : '';
    this.itemsList = this.filterList(this.items, query);
  }
 filterList(list, query): Array<any> {
   return list.filter(item => item.nombre.toLowerCase().includes(query.toLowerCase()));
  }
  async otrasop(item: any) {
      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
       header: 'Seleccione Opción',
       inputs: [
         {
           name: 'Productos',
           type: 'radio',
           label: 'Productos',
           checked: true,
           value: '/ProductoListPage°Productos ->' + item.nombre,
         }
       ],
       buttons: [
         {
           text: 'Cancel',
           role: 'cancel',
           cssClass: 'secondary',
           handler: (todo) => {
             console.log(todo);
           }
         }, {
           text: 'Ok',
           handler: (todo) => {
             const arr = todo.split('°');
             const navigationExtras: NavigationExtras = {
               state: {
                 variables: this.variables,
                 user: null,
                 idllave: item._id,
                 title: arr[1]
               }
             };
             this.router.navigate([arr[0]], navigationExtras);
           }
         }
       ]
     });
     await alert.present();
 }
   async deleteRecord(item: any) {
    const alert = await this.alertController.create({
      header: this.seguro,
      message: '',
      buttons: [
        {
          text: 'No',
         cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
           text: 'Si',
           handler: () => {
             const recordID: string		= item._id;
             this.authenticationService.deleteReg(recordID + '/' + this.userinfo[0].email + '/' + this.userinfo[0].idempresa._id  + '/'
             + 'na' , 'personals').then((data) => {
               this.pideregistros( this.currentSegment);
               }, (err) => {
                 this.authenticationService.presentAlert(err.error, this.msg, '');
             });
           }
         }
       ]
     });
     await alert.present();
   }
  async updateRecord(item: any) {
    const modal = await this.modalController.create({
      component: UserCreatePage,
      swipeToClose: true,
      presentingElement: this.routerOutlet.nativeEl,
      componentProps:  { ndisp:this.ndisp, iddispositivo:this.iddispositivo,  idasociado: this.idasociado,variables:  this.variables, record: item, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data !== 'close' ) {
      this.pideregistros('todos');
      }
    });
    return await modal.present();
}
   async  addRecord() {
    const modal = await this.modalController.create({
      component: UserCreatePage,
      swipeToClose: false,
      backdropDismiss: false,
     animated: true,
      presentingElement: await this.modalController.getTop(),
      componentProps:  { ndisp:this.ndisp,iddispositivo:this.iddispositivo, idasociado: this.idasociado,variables:  this.variables, record: null, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data.estado !== 'close' ) {
        

        //  this.pideregistros('todos');
          this.authenticationService.presentAlert('Exito', data.data.msm , '');
      }
    });
    return await modal.present();
   }
   loadData(event) {
    setTimeout(() => {
      event.target.complete();
      if ( this.items.length === 1000) {


    this.page++;
    this.pideregistros( this.currentSegment , event);

    if (this.page === this.maximumPages) {
      event.target.disabled = false;
    } else {  event.target.disabled = true; }



      }
    }, 500);
  }
}
