  async dacomponente(op, op2) {
        let uub = '';
        if ( op === 'ubicacion') {uub = this.Form.controls['ubicacion'].value; }
        const modal = await this.modalController.create({
          component: MyModalPage,      swipeToClose: false,    backdropDismiss: false,     animated: true,
          presentingElement: await this.modalController.getTop(),
          componentProps:  { variables:  this.variables, record: null, userinfo: this.userinfo, uubx : uub}
          // , cssClass: 'modal-fullscreen'
        });
        modal.onWillDismiss().then((data) => {
             if ( data.data === 'close' ) {     }      else      {
             if (op === 'direccion') {
              this.Form.controls['direccion'].setValue(data.data.direccion);
             } else {
              this.Form.controls['ubicacion'].setValue(data.data.direccion + ' {' + data.data.la + '°' + data.data.lo + '}');
             }
            }
        });
return await modal.present();
      }
